define('web-desktop/controllers/application', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    loginShow: false,
    appMoving: false,
    actions: {
      loginShow: function () {
        this.set('loginShow', true);
      },
      loginClose: function () {
        this.set('loginShow', false);
      }
    }
  });

});